import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { ProcessAccountClosureComponent } from './process-account-closure/process-account-closure.component';

const routes: Routes = [
  {
    path:"",
    component:ProcessAccountClosureComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ProcessAccountClosureRoutingModule { }
